import { useState, useEffect } from 'react';
import { Palette, Share } from 'lucide-react';
import { BannerShareButton } from './BannerShareButton';
import type { UserProfile } from '../types';

interface BannerStyle {
  id: string;
  name: string;
  style: string;
  levelRequired: number;
  description: string;
}

export const defaultBanners: BannerStyle[] = [
  {
    id: 'banner-masculine',
    name: 'Earth Force',
    style: 'banner-masculine',
    levelRequired: 0,
    description: 'A bold, dynamic gradient inspired by natural strength'
  },
  {
    id: 'banner-feminine',
    name: 'Gentle Dawn',
    style: 'banner-feminine',
    levelRequired: 0,
    description: 'A soft, harmonious blend of warm earth tones'
  },
  {
    id: 'banner-neutral',
    name: 'Golden Balance',
    style: 'banner-neutral',
    levelRequired: 0,
    description: 'A balanced composition of earthy gold and warm beige'
  },
  {
    id: 'banner-advanced-masculine',
    name: 'Mountain Force',
    style: 'banner-masculine',
    levelRequired: 5,
    description: 'An advanced dynamic gradient for seasoned members'
  },
  {
    id: 'banner-advanced-feminine',
    name: 'Forest Dawn',
    style: 'banner-feminine',
    levelRequired: 10,
    description: 'A premium blend of natural tones for experienced members'
  },
  {
    id: 'banner-advanced-neutral',
    name: 'Mystic Balance',
    style: 'banner-neutral',
    levelRequired: 15,
    description: 'An elite composition unlocked through dedication'
  }
];

interface ProfileBannerProps {
  userId: string;
  selectedBanner?: string;
  userLevel: number;
  editable?: boolean;
  profile: UserProfile;
  onUpdate: (bannerId: string) => Promise<void>;
}

// Preload banners to ensure smooth transitions
const preloadBanner = (bannerId: string) => {
  const div = document.createElement('div');
  div.style.position = 'absolute';
  div.style.opacity = '0';
  div.style.pointerEvents = 'none';
  div.className = bannerId;
  document.body.appendChild(div);
  requestAnimationFrame(() => {
    document.body.removeChild(div);
  });
};

export function ProfileBanner({ 
  userId, 
  selectedBanner, 
  userLevel, 
  editable = false,
  profile,
  onUpdate 
}: ProfileBannerProps) {
  const [isCustomizing, setIsCustomizing] = useState(false);
  const [previewBanner, setPreviewBanner] = useState(selectedBanner);
  const [isTransitioning, setIsTransitioning] = useState(false);
  
  // Preload all available banners
  useEffect(() => {
    defaultBanners.forEach(banner => {
      if (banner.levelRequired <= userLevel) {
        preloadBanner(banner.style);
      }
    });
  }, [userLevel]);

  // Update preview when selected banner changes
  useEffect(() => {
    setPreviewBanner(selectedBanner);
  }, [selectedBanner]);

  const getBannerClass = (bannerId?: string) => {
    const baseClass = bannerId ? 
      defaultBanners.find(b => b.id === bannerId)?.style : 
      'banner-default';
    
    return `${baseClass} transition-all duration-500 ease-in-out ${
      isTransitioning ? 'opacity-0 scale-105' : 'opacity-100 scale-100'
    }`;
  };

  const handleBannerSelect = async (bannerId: string) => {
    setIsTransitioning(true);
    setPreviewBanner(bannerId);
    
    // Wait for fade out
    await new Promise(resolve => setTimeout(resolve, 250));
    
    // Update banner
    await onUpdate(bannerId);
    
    // Wait for fade in
    setTimeout(() => {
      setIsTransitioning(false);
    }, 250);
  };

  // Filter and sort banners
  const availableBanners = defaultBanners
    .filter(b => b.levelRequired <= userLevel)
    .sort((a, b) => a.levelRequired - b.levelRequired);
  
  const upcomingBanners = defaultBanners
    .filter(b => b.levelRequired > userLevel)
    .sort((a, b) => a.levelRequired - b.levelRequired);

  // Debug info with sorted banners
  console.log('ProfileBanner render:', {
    userLevel,
    editable,
    selectedBanner,
    previewBanner,
    availableBanners,
    upcomingBanners
  });

  if (availableBanners.length === 0) {
    console.warn('No available banners for level:', userLevel);
  }

  return (
    <div className="relative">
      <div className="profile-banner relative">
        <div 
          className="absolute inset-0"
          style={{
            background: previewBanner?.includes('masculine')
              ? 'linear-gradient(135deg, #4A5D43 0%, #2F3B2F 100%)'
              : previewBanner?.includes('feminine')
              ? 'linear-gradient(135deg, #C67D63 0%, #8B9D83 100%)'
              : previewBanner?.includes('neutral')
              ? 'linear-gradient(135deg, #D69F4C 0%, #F5F2EA 100%)'
              : '#1F2937'
          }}
        />
        {editable && (
          <button 
            onClick={() => setIsCustomizing(true)}
            className="absolute top-2 right-2 p-2 rounded-full bg-white/10 backdrop-blur-sm
                     hover:bg-white/20 transition-all"
            aria-label="Customize Banner"
            title="Click to customize banner"
          >
            <Palette className="h-5 w-5 text-white" />
          </button>
        )}
      </div>

      {isCustomizing && (
        <div 
          className="fixed inset-0 bg-black/80 flex items-center justify-center z-50 p-4"
          role="dialog"
          aria-modal="true"
          aria-labelledby="banner-dialog-title"
        >
          <div className="bg-earth-800 rounded-lg shadow-xl max-w-3xl w-full overflow-hidden">
            <div className="flex justify-between items-center border-b border-earth-700 p-4">
              <h3 id="banner-dialog-title" className="text-lg font-display font-semibold text-white">
                Choose Your Banner
              </h3>
              <button
                onClick={() => {
                  setIsCustomizing(false);
                  setPreviewBanner(selectedBanner);
                }}
                className="p-2 rounded-full hover:bg-earth-700 transition-colors"
                aria-label="Close dialog"
              >
                <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            <div className="p-8 space-y-8">
              {/* Banner Preview */}
              <div 
                className="profile-banner mb-6"
                style={{
                  background: previewBanner?.includes('masculine')
                    ? 'linear-gradient(135deg, #4A5D43 0%, #2F3B2F 100%)'
                    : previewBanner?.includes('feminine')
                    ? 'linear-gradient(135deg, #C67D63 0%, #8B9D83 100%)'
                    : previewBanner?.includes('neutral')
                    ? 'linear-gradient(135deg, #D69F4C 0%, #F5F2EA 100%)'
                    : 'var(--earth-800)'
                }}
              />

              {/* Available Banners */}
              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <h4 className="text-sm font-medium text-gray-300">Available Styles</h4>
                  <span className="text-xs text-gray-500">Your Level: {userLevel}</span>
                </div>
                <div className="banner-selector rounded-lg border border-earth-700">
                  {availableBanners.length === 0 && (
                    <div className="text-center py-8">
                      <p className="text-gray-400">No banners available yet</p>
                      <p className="text-gray-500 text-sm mt-2">Level up to unlock banners!</p>
                    </div>
                  )}
                  {defaultBanners
                    .filter(banner => {
                      // Debug info
                      console.group('Banner Availability Check');
                      console.log('Banner:', banner.name);
                      console.log('Banner Level Required:', banner.levelRequired);
                      console.log('User Level:', userLevel);
                      console.log('Is Available:', banner.levelRequired <= userLevel);
                      console.groupEnd();
                      
                      return banner.levelRequired <= userLevel;
                    })
                    .map(banner => (
                      <button
                        key={banner.id}
                        className={`banner-option transform transition-all duration-300 relative ${
                          previewBanner === banner.id 
                            ? 'selected scale-95' 
                            : selectedBanner === banner.id
                            ? 'border-accent-clay/50'
                            : ''
                        }`}
                        onMouseEnter={() => {
                          console.log('Hovering banner:', banner.name);
                          setPreviewBanner(banner.id);
                        }}
                        onMouseLeave={() => setPreviewBanner(selectedBanner)}
                        onClick={() => {
                          console.log('Selected banner:', banner.name);
                          handleBannerSelect(banner.id);
                        }}
                        style={{
                          background: `
                            ${banner.style === 'banner-masculine' 
                              ? 'linear-gradient(135deg, #4A5D43 0%, #2F3B2F 100%)'
                              : banner.style === 'banner-feminine'
                              ? 'linear-gradient(135deg, #C67D63 0%, #8B9D83 100%)'
                              : 'linear-gradient(135deg, #D69F4C 0%, #F5F2EA 100%)'
                            }`,
                          backgroundSize: '200% 200%',
                          animation: 'gradient 8s ease infinite'
                        }}
                        title={`${banner.name} - ${banner.description}`}
                      >
                        <div className="absolute inset-0 flex flex-col items-center justify-center p-2">
                          <span>{banner.name}</span>
                          <span>{banner.description}</span>
                        </div>
                      </button>
                  ))}
                </div>
                
                {defaultBanners.filter(banner => banner.levelRequired <= userLevel).length === 0 ? (
                  <div className="text-center p-4 bg-earth-800/50 rounded-lg">
                    <p className="text-text-secondary">No banners available at your current level.</p>
                    <p className="text-text-muted text-sm mt-2">
                      Level up to unlock more banner styles!
                    </p>
                  </div>
                ) : (
                  <div className="mt-6 bg-earth-800/30 rounded-lg p-4">
                    <h5 className="text-sm font-medium text-gray-400 mb-3">Upcoming Banners</h5>
                    <div className="space-y-3">
                      {defaultBanners
                        .filter(banner => banner.levelRequired > userLevel)
                        .map(banner => (
                          <div key={banner.id} className="flex items-center justify-between p-2 rounded bg-earth-800/50 border border-earth-700">
                            <div className="flex items-center gap-3">
                              <div className="w-8 h-8 rounded" style={{
                                background: banner.style === 'banner-masculine'
                                  ? 'linear-gradient(135deg, #4A5D43 0%, #2F3B2F 100%)'
                                  : banner.style === 'banner-feminine'
                                  ? 'linear-gradient(135deg, #C67D63 0%, #8B9D83 100%)'
                                  : 'linear-gradient(135deg, #D69F4C 0%, #F5F2EA 100%)'
                              }} />
                              <span className="text-sm font-medium text-gray-300">{banner.name}</span>
                            </div>
                            <span className="text-sm px-2 py-1 rounded bg-earth-900/50 text-gray-400">Level {banner.levelRequired}</span>
                          </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>

              {/* Actions */}
              <div className="mt-6 flex justify-end gap-2">
                <BannerShareButton
                  profile={profile}
                  bannerId={previewBanner || selectedBanner || 'banner-default'}
                />
                <button
                  className="btn-secondary"
                  onClick={() => {
                    setIsCustomizing(false);
                    setPreviewBanner(selectedBanner);
                  }}
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
